.contain{
    background: #f6f6f6;
    margin-top: 2rem;
    margin-bottom: 2rem;
    border-radius: 20px;
    display: flex;
    flex-wrap: wrap;
}

@media (max-width:1440px){
    .contain{
        justify-content: space-between;
        padding: 3rem;
    }
}

@media (max-width:1100px){
    .contain{
        justify-content: center;
    }
}

