$colour-primary: #ff6060;
$colour-secondary: #f6f6f6;

.Appheader {
  // border: solid 2px red;
  height: 6rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-left: 1rem;
  

  &__logo {
    // border: solid 2px red;
    padding-top: 1rem;
    width: 190px;
    height: 68px;
  }

  &__nav {
    // border: solid 2px blue;
    width: 16rem;
    margin-left: 70%;

    &--list {
      // border: solid 2px green;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-left: 1rem;
      margin-top: 0;
      list-style-type: none;
      height: 100%;     
    }

    &--listLink{
      text-decoration: none;
    }

    &--listItem {
      margin-right: 10px;
      color: $colour-primary;
      cursor: pointer;
    }

    &--listItem:hover{
      border-bottom: solid 2px #ff6060;
    }

  }
}

// RESPONSIVE..................................

@media (max-width:1440px){
  .Appheader {
    &__nav {
      width: 12rem;
      margin-left: 60%;
    }
  }
}

@media (max-width:1110px){
  .Appheader {
    &__nav {
      &--listItem {
        font-size: 16px;
      }
    }
  }
}

@media (max-width:1040px){
  .Appheader {
    &__nav {
      margin-left: 50%;
    }
  }
}

@media (max-width:870px){
  .Appheader {
    &__nav {
      margin-left: 40%;
    }
  }
}

@media (max-width:755px){
  .Appheader {
    &__nav {
      margin-left: 30%;
    }
  }
}

@media (max-width:620px){
  .Appheader {

    &__logo {
      width: 170px;
      height: 61px;
    }

    &__nav {
      margin-left: 20%;

      &--listItem {
        font-size: 14px;
        text-transform: uppercase;
      }
    }
  }
}

@media (max-width:540px){
  .Appheader {

    &__logo {
      width: 100px;
      height: 50px;
    }

    &__nav {
      margin-left: 10%;

      &--listItem {
        text-transform: uppercase;
      }
    }
  }
}

@media (max-width:470px){
  .Appheader {

    &__logo {
      margin-top: 1rem;
    }

    &__nav {
      margin-left: 5%;

      &--list {
        padding-left: 2rem;
        height: 100%;     
      }

      &--listItem {
        font-size: 13px;
      }
    }
  }
}

@media (max-width:460px){
  .Appheader {

    &__nav {
      margin-left: 0;

      &--listItem {
        font-size: 12px;
      }
    }
  }
}


  
